import React from 'react';
import { useForm } from 'react-hook-form';
import styles from './SignUpForm.module.scss';
import { useError } from '../../components/ErrorProvider/ErrorProvider';

interface SignUpFormInputs {
  userName: string;
  password: string;
  confirmPassword: string;
}

interface SignUpFormProps {
  onSignUp: (data: SignUpFormInputs) => void;
}

const SignUpForm: React.FC<SignUpFormProps> = ({ onSignUp }) => {
  const {
    register,
    handleSubmit,
    watch,
    formState: { errors },
  } = useForm<SignUpFormInputs>();
  const { errorData } = useError();
  const password = watch('password');

  return (
    <div className={styles.container}>
      <form className={styles.form} onSubmit={handleSubmit(onSignUp)}>
        <h1 className={styles.title}></h1>
        {errorData?.status && (errorData?.status >= 401 || errorData?.status <= 499) && (
          <p className={styles.apiError}>{errorData?.message}</p>
        )}
        <div className={styles.field}>
          <label htmlFor="userName" className={styles.label}>
             
          </label>
          <input
            id="userName"
            type="text"
            className={styles.input}
            {...register('userName', {
              required: '  ',
              pattern: {
                value: /^[a-zA-Z0-9]+$/,
                message: '       ',
              },
            })}
          />
          {errors.userName && <p className={styles.error}>{errors.userName.message}</p>}
        </div>
        <div className={styles.field}>
          <label htmlFor="password" className={styles.label}>
            
          </label>
          <input
            id="password"
            type="password"
            className={styles.input}
            {...register('password', {
              required: ' ',
              minLength: { value: 6, message: '    6 ' },
            })}
          />
          {errors.password && <p className={styles.error}>{errors.password.message}</p>}
        </div>
        <div className={styles.field}>
          <label htmlFor="confirmPassword" className={styles.label}>
             
          </label>
          <input
            id="confirmPassword"
            type="password"
            className={styles.input}
            {...register('confirmPassword', {
              required: ' ',
              validate: (value) => value === password || '  ',
            })}
          />
          {errors.confirmPassword && <p className={styles.error}>{errors.confirmPassword.message}</p>}
        </div>
        <button type="submit" className={styles.button}>
          
        </button>
      </form>
    </div>
  );
};

export default SignUpForm;
