import React, { createContext, useCallback, useContext, useEffect, useState } from 'react';
import {
  applyTheme,
  applyThemeVariables,
  findTheme,
  getThemeVariableValues,
  injectGlobalThemes,
  Theme,
  Themes,
  ThemeStyleValues,
} from './ThemeUtils';

interface ThemeContextProps {
  theme: Theme;
  themeValues: ThemeStyleValues;
  toggleTheme: () => void;
}

const ThemeContext = createContext<ThemeContextProps | undefined>(undefined);

export interface ThemProviderProps {
  children: React.ReactNode;
}

export const ThemeProvider: React.FC<ThemProviderProps> = ({ children }) => {
  const [theme, setTheme] = useState<Theme>(findTheme(document.body));
  const [themeValues, setThemeValues] = useState(getThemeVariableValues(theme));

  //  
  useEffect(() => {
    injectGlobalThemes(); //         global
  }, []);

  //   
  useEffect(() => {
    const values = getThemeVariableValues(theme);
    applyTheme(document.body, theme); //   body   
    applyThemeVariables(document.documentElement, values); //       root
    setThemeValues(values); //         context
  }, [theme]);

  //   
  const toggleTheme = useCallback(() => {
    const newTheme = theme === Themes.Light ? Themes.Dark : Themes.Light;
    applyTheme(document.body, newTheme); //    (  global)
    setTheme(newTheme);
  }, [theme]);

  return <ThemeContext.Provider value={{ theme, themeValues, toggleTheme }}>{children}</ThemeContext.Provider>;
};

export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useTheme    ThemeProvider');
  }
  return context;
};
